  display: grid;
  grid-template-columns: 1fr;
  gap: 20px;
}
@media (min-width: 768px) {
  .formulario-sugerencias form {
    grid-template-columns: repeat(2, 1fr);
  }
  .formulario-sugerencias form .sugerencia {
    grid-column: span 2;
  }
}
.formulario-sugerencias form .user-box {
  position: relative;
}
