.formulario-sugerencias form .user-box textarea {
  width: 100%;
  padding: 10px 0;
  font-size: 16px;
  color: #000;
  border: none;
  border-bottom: 1px solid #000;
  outline: none;
  background: transparent;
}
.formulario-sugerencias form .user-box label {
  position: absolute;
  top: 0;
  left: 0;
  padding: 10px 0;
  font-size: 16px;
  color: #000;
  pointer-events: none;
  transition: 1s;
}
.formulario-sugerencias form .user-box input:focus ~ label,
.formulario-sugerencias form .user-box input:valid ~ label,
.formulario-sugerencias form .user-box textarea:focus ~ label,
.formulario-sugerencias form .user-box textarea:valid ~ label {
  top: -20px;
  left: 0;
  color: #333333;
  font-size: 12px;
}
.formulario-sugerencias form .Enviar {
  width: 100%;
  background-color: #e74c3c;
  color: #FFFFFF;
  font-size: 1.2rem;
  font-weight: bold;
  padding: 1rem;
