.Fondo-Secundario {
    display: flex;
    flex-direction: column;
    align-items: center;
    position: relative;
    width: 100%;
    height: 100%;

    .Overplay-Secundario {
        position: absolute;
        inset: 0;
        background: linear-gradient(
            270deg,
            rgba($negro, 25%),
            rgba(darken($negro, 80%), 80%)
        );
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center; // Centra verticalmente
        text-align: center;      // Centra el texto
    }

    img {
        max-width: 100%;
        height: auto;
        @include tablet {
            max-width: 100%;
        }
        @include desktop {
            max-width: 100%;
        }
    }

    h2 {
        font-size: 2.5rem;
        color: $blanco;
        font-family: $Fuente_Principal;
        font-weight: bold;
        @include tablet {
            font-size: 7rem;   
        }
        @include desktop {
            font-size: 9rem;
        }
    }

    p {
        font-family: Arial, Helvetica, sans-serif;
        font-size: 2rem;
        color: $blanco;
        margin-top: -2rem;
        @include tablet {
            font-size: 4rem;
            margin-top: -8rem;
        }
        @include desktop {
            font-size: 6rem;
            margin-top: -8rem;
        }
    }
}

