.footer {
  background-color: $GreenPine;
  padding: 2rem 0;
  display: flex;
  justify-content: center; /* Cambiado a center para controlar mejor el layout */
  align-items: center;

  .contenedor-footer {
    width: 100%; /* Asegura que ocupe todo el ancho */
    display: flex;
    flex-direction: row;
    justify-content: space-between; /* Para que el contenido esté bien distribuido */

    .navegacion-footer {
      display: flex;
      flex-direction: row;

      a {
        font-size: 1rem;
        color: $blanco;
        font-family: Arial, Helvetica, sans-serif;
        font-weight: bold;
        text-decoration: none;
        padding: 0.5rem;

        &:hover {
          transition: 1s ease;
          color: $Golden;
        }

        @include tablet {
          font-size: 1.5rem;
          &:hover {
            transition: 1s ease;
            color: $Golden;
            border-radius: 2vh;
          }
        }

        @include laptop {
          font-size: 2rem;
        }

        @include desktop {
          font-size: 2.5rem;
        }
      }
    }

    .social-icons {
      display: flex;
      flex-direction: row;
      justify-content: end;
      margin-left: auto; /* Esto empuja los íconos hacia la derecha */
      
      .social-icon {
        display: flex;
        justify-content: center;
        align-items: center;
        text-decoration: none;
        margin: 0.5rem;
        padding: 0.5rem;
        background-color: $blanco;
        border-radius: 50%;

        svg {
          fill: currentColor; // Hace que el SVG herede el color del texto
        }

        @include tablet {
          width: 5rem;
          height: 5rem;
          font-size: 5rem;
        }
      }
    }

    .Instagram {
      color: $grisOscuro;
    }
    .Instagram:hover {
      transition: 1s ease;
      background: linear-gradient(45deg, #FCAF45, #F77737, #C13584, #833AB4, #405DE6);
      color: $blanco;
    }

    .Facebook {
      color: $grisOscuro;
    }
    .Facebook:hover {
      transition: 1s ease;
      color: #0966FF;
    }
    .X {
      color: $grisOscuro;
      &:hover {
        transition: 1s ease;
        color: $negro;
      }
    }
  }
}
