@include laptop {
    .seccion-inferior {
        display: grid;
        grid-template-columns: 2fr 1fr;
        column-gap: 2rem;
    }
}

.Nuestras-Propiedades {
    text-align: center;
    font-family: $Fuente_Principal;
    font-size: 2.5rem;
}

.Entrada-arte {
    margin-bottom: 2rem;
    display: block;
    @include laptop {
        display: grid;
        grid-template-columns: 1fr 2fr;
        column-gap: 2rem;
    }
    &:last-of-type {
        margin-bottom: 0;
    }

    .imagen {
        margin: 1rem;
        img {
            width: 100%;
            height: auto;
        }
    }
}

.texto-entrada {
    margin: 1rem;
    h4 {
        font-size: 2rem;
        font-family: $Fuente_Principal;
        margin: 0;
        line-height: 1.4;
        &::after {
            content: '';
            display: block;
            width: 15rem;
            height: 0.5rem;
            background-color: $GreenPine;
            margin-top: 1rem;
        }
    }
    p {
        font-family: Arial, Helvetica, sans-serif;
        font-size: 1.5rem;
    }
    span {
        font-family: Arial, Helvetica, sans-serif;
        font-weight: bold;
        font-size: 1.5rem;
        color: $GreenPine;
        margin-left: 1rem;
    }
}