@include laptop{
    .contenedor-anuncios {
        display: grid;
        grid-template-columns: repeat(3, 1fr);
    }
}

h2 {
    font-family: Arial, Helvetica, sans-serif;
    text-align: center;
    font-size: 2.5rem;
}

.anuncio{
    background-color: $gris;
    border: 1px solid $grisOscuro;
    margin: 1rem;

    img {
        width: 100%;
        height: 30rem;
    }
    .contenido-anuncio{
        padding: 2rem;
    }

    h3 {
        text-align: center;
        font-size: 1.5rem;
        font-family: $Fuente_Principal;
        font-weight: bold;
        line-height: 1;
        margin: 0;

        @include laptop {
        font-size: 2.5rem;
        }
    }
    p {
        margin: 1rem;
        line-height: 1.5;

        @include laptop {
            font-size: 1.5rem;
            }
    }
    .precio {
        color: $GreenPine;
        font-weight: bold;
        font-size: 3rem;
    }
}