html {
  scroll-behavior: smooth;
}


.header {
  display: flex;
  flex-direction: column;
  position: relative;
  width: 100%;
  height: 100vh;
  position: relative;

  .contenido-header {
      display: flex;
      flex-direction: column;
      justify-content: flex-end;

      // Logo
      .Keys-Homes-Logo{
          width: 15rem;
          height: auto;
          margin: 0rem;
          cursor: pointer;

          @include tablet {
            width: 15rem;
          }

        @include desktop{
          width: 25rem;
         
        }
      }

      .mobile-menu {
        display: flex;
        justify-content: center;
        align-items: center;
        margin-top: 1.5rem;
        cursor: pointer;
  
        @include tablet {
            display: none;
        }
  
        img {
            width: 5rem;
            height: auto;
        }
    }

      // Navegación
      .navegacion {

          top: 16rem;
          left: 0;
          width: 100%;
          text-align: center;
          max-height: 0;
          overflow: hidden;
          opacity: 0;
          transition: max-height 1s ease, opacity 1s ease;
          z-index: 1000;

          a {
              display: block;
              font-size: 1rem;
              color: $blanco;
              font-family:Arial, Helvetica, sans-serif;
              font-weight: bold;
              text-decoration: none;
              padding: 0.1rem;
              justify-content: center;
              margin-top: 0.5rem;

              &:hover {
                transition: 1s ease;
                  color: $Golden;
                 
              }
              
              @include tablet {
                justify-content: center;
                font-size: 1.5rem;
                padding: 0.5rem;
                &:hover {
                  transition: 1s ease;
                  color: $Golden;
                  border-radius: 2vh;
                }
              }

              @include laptop {
                font-size: 2rem;
              }

              @include desktop {
                font-size: 2.5rem;
              }

          }

          &.mostrar {
              max-height: 15rem;
              opacity: 1;
          }

          @include tablet {
              max-height: none;
              opacity: 1;
              display: flex;
              flex-direction: row;
              justify-content: center;
          }

          @include desktop {
            max-height: none;
            opacity: 1;
            display: flex;
            flex-direction: row;
            align-items: flex-start;
            justify-content: center;
          }
      }
  }

  .general-information-KeysHomes {
    display: grid;
    grid-template-columns: 1fr; 
  
    @include laptop {
      grid-template-columns: repeat(2, 1fr); 
    }
  
    .information-KeysHomes {
      display: flex;
      flex-direction: column;
      justify-content: center;

      h3 {
        text-align: center;
        font-size: 1.5rem;
        padding: 1rem;
        margin-top: -0.2rem;
        font-family: $Fuente_Principal;
        color: $Golden;

        @include tablet {
          font-size: 2rem;
        }
        @include desktop {
          font-size: 3rem;
        }
      }
  
      h1 {
        text-align: center;
        font-size: 2.4rem;
        font-family: $Fuente_Principal;
        color: $blanco;
        margin: -2.5rem 0 0 1rem;
        line-height: 1;

        .transparent-text {
          opacity: 0.5;
          color: $greenAqua;
        }

        @include tablet {

          font-size: 2.5rem;
        }

        @include desktop {
          margin-top: -3.5rem;
          font-size: 4.9rem;
        }
      }
  
      p {
        font-family: Arial, Helvetica, sans-serif;
        font-size: 1rem;
        text-align: center;
        color: $blanco;
        padding: 1rem;
        margin: 1rem;

        @include tablet {
          font-size: 2rem;
        }

        @include desktop {
          margin-top: 0.5rem;
          font-size: 2.5rem;
        }
       
      }
    }

    .botones-info {
      display: flex;
      flex-direction: column;
      justify-content: space-between;
      margin: -1rem 1rem 0 1rem;
  
      @include tablet {
        flex-direction: row;
        justify-content: flex-end;
      }

      @include laptop{
        flex-direction: row;
        justify-content: space-between;
      }
  
      .Ver-Propiedades {
        font-family: $Fuente_Principal;
        font-size: 1rem;
        background-color: $greenAqua;
        border-radius: 3vh;
        border-style: none;
        margin-bottom: 1rem;
        padding: 1rem;

        cursor: pointer;
        &:hover {
          transition: 1s ease;
          background-color: #506b62;
          color: $blanco;
        }
        a {
        color: $blanco;
        text-decoration: none;
        padding: 1rem;
        }

        @include tablet {
          margin-right: 1rem;
          margin-bottom: 0;
        }

        @include laptop{
          width: 100%;
          height: auto;
        }
      }
  
      .Leer-más {
        font-family: $Fuente_Principal;
        font-size: 1rem;
        background-color: transparent;
        border: $blanco solid 0.2rem;
        border-radius: 3vh;
        padding: 1rem;

        cursor: pointer;
        
        &:hover {
            transition: background-color 1s ease;
            background-color: $blanco;
        }
        
        a {
            color: $blanco;
            text-decoration: none;
            padding: 1rem;
            
            &:hover {
                transition: color 1s ease;
                color: $negro;
            }
        }
    
        @include laptop {
            width: 100%;
            height: auto;
        }
      }
    
    }

    .Edificio {
      //Aqui es donde esta el problema
      width: 30%; 
      height: auto;
      position: absolute;
      bottom: 0;

      @include tablet{
        width: 25%; 
        height: auto;
        bottom: 0;
      }

      @include laptop {
        width: 30%; 
        height: auto;
        bottom: 0;
        right: 0;
      }
  
      @include desktop {
        width: 30%; /* Ajusta el ancho en pantallas grandes */
        height: auto;
        bottom: 0;
        right: 0;
      }
    }
  }
  
  .fondo-principal {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      z-index: -1; /* Envía el fondo detrás del contenido */
  }
}
