.Nuestros-blog{
    color: $negro;
    font-size: 5rem;
    font-weight: bold;
    font-family: $Fuente_Principal;
    text-align: center;

    @include desktop {
        font-size: 8rem;
    }
}

.seccion-blog {
    display: flex;
    text-align: start;
    margin: 1rem;

    .Entrada-blog {
        margin-bottom: 2rem;
   
        &:last-of-type {
            margin-bottom: 5rem;
        }

        img {
            width: 100%;
            height: auto;
        }
    }
    
    .texto-blog {
        h4 {
            font-family: $Fuente_Principal;
            margin: 1rem 0 0 0;
            line-height: 1.4;
    
            &::after {
                content: '';
                display: block;
                width: 15rem;
                height: 0.5rem;
                background-color: $GreenPine;
                margin-top: 1rem;
            }
        }
        span {
            color: $GreenPine;
            font-weight: bold;
            font-family: $Fuente_Principal;
        }

        p {
            font-family: Arial, Helvetica, sans-serif;
            font-weight: normal;
        }
    }
}

