.contenedor {
  width: 95%;
  max-width: 120rem;
  margin: 0 auto;
}

.contenedor-propiedades-row {
  @include laptop {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
  }
}



.propiedades {
  background-color: $gris;
  border: 1px solid $grisOscuro;
  padding: 1rem;
  margin: 0 1rem 1.5rem 1rem;
  margin-bottom: 1.5rem; // Espacio entre los bloques en la vista de masonry
  break-inside: avoid; // Evitar que se corten los elementos
}

.propiedades img {
  width: 100%;
  height: 25rem;
}

.contenido-propiedades {
  margin: 1rem;

  h3 {
      text-align: center;
      font-size: 1.5rem;
      font-family: $Fuente_Principal;
      font-weight: bold;
      line-height: 1;
      margin: 0;

      @include laptop {
      font-size: 2.5rem;
      }
  }
  p {
      margin: 1rem;
      line-height: 1.5;

      @include laptop {
          font-size: 1.5rem;
          }
  }
  .precio {
      color: $GreenPine;
      font-weight: bold;
      font-size: 3rem;
  }
}
